}
.main p{
    margin-top: 1rem;
    padding: 0;
    font-size: 26px;
    color: #FFf;
    padding-bottom: 25px;
}
.main .readmore{
    background: #769371;
    color: #FFf;
    border-radius:5px ;
    padding:20px 50px ;
}
.icon-color{
    color:green;
    font-size: 20px;
}
.icon-color:hover{
    color:red;
}
